
@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;700&family=Poppins&family=Roboto&display=swap');
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
*{
  margin: 0;padding: 0;box-sizing: border-box;
}
#root{
  height: 100vh;
  width: 100vw;
  background-color: #F0F0F0;
  padding: 1px;
}

.form{
  border-radius: 10px;
  box-shadow: 4px 4px 10px rgb(184, 168, 168);

  background-color: white;
  display: flex;
  height: 540px;
  flex-direction: column;
  width: 90%;
  max-width: 600px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  padding: 20px;
  /* border: 1px solid black; */


}
.form div{
  padding: 10px;
}
.form div span{
  display:block;
  position: relative;
  left: 138px;
  font-size: 12px;
  color: red;

  top: 10px;
}
.form div:nth-child(2){
  margin-top: 20px;
}
.form div:nth-child(5) span{
  position: inherit;
  display: inline-block;
  margin-left: 10px;
}
.form span{
  color: red;
  margin-left: 20px;
  padding-top: 10px;
}
.form div label{
  width: 130px;
  display: inline-block;
  font-family: "Roboto",sans-serif;
font-weight: bold;
}
.form div input{
  width: calc(90% - 120px);
  border: none;
  border-bottom: 1px solid hwb(0 87% 13%);
  padding: 10px;
  outline: none;
  font-family: "Roboto",sans-serif;
}
.form img{
  height: 40px;
  width: 40px;

}
.imgDiv{
  align-self: center;
  position: absolute;
  margin-top: -55px;
  background-color: #F0F0F0;
  border-radius: 50%;
  box-shadow: 4px 4px 10px rgb(184, 168, 168);
}
textarea{
  outline: none;
  border: 4px solid #F0F0F0;
  padding: 10px;
  width: 95%;
  align-self: center;
  margin-bottom: 10px;
}
#submitBtn{
  width: 170px;
  background-color: #F0F0F0;
  border: none;
  border-radius: 50px;
  font-weight: bold;
  font-family: "Roboto",sans-serif;
  height: 50px;
  align-self: center;

}
@media screen and (max-width:1930px) and (min-width:1300px) {
   textarea{
    width: 98%;
   }  
}